<!--/-->
<style lang="scss" rel="stylesheet/scss">
    @import "../../../utils/mixins/common";
    #medical-form {
        @extend %content-container;
        display: flex;
  
        .upload-avatar{
            width:30%;
            text-align: center;
            .img-container {
            margin: 20px auto;
            width: 170px;
            height: 170px;
            border: 1px solid #ccc;
            border-radius:50%;
                >img {
                    width: 100%;
                    height: 100%;
                    border-radius: 50%;
                    background: #ededed;
                }
            }
        }
        .submit-form {
        width: 45%;
        padding: 20px;
            .select{
                width: 100%;
            }
            .subButton {
                text-align: center;
            }
            div{
                width: 100%;
            }
    }
    }
</style>
<template>
    <main id="medical-form">
        <section class="upload-avatar">
            <div class="img-container">
                <img :src="fetchParam.image|fillImgPath" v-show="fetchParam.image"/>
                 <!--<img :src="{url:clerkDetail.avatar, sex: clerkDetail.sex} | defaultAvatar" />-->
            </div>
            <ImagEcropperInput :isRound="true" :aspectRatio="1" :confirmFn="cropperFn" class="upload-btn"></ImagEcropperInput>
        </section>     
        <section class="submit-form">   
            <el-form label-width="120px" ref="form" :rules="rules" :model="fetchParam" >
                
                <el-form-item label="" prop="name">
                    <el-input v-model.name="fetchParam.name"></el-input>
                </el-form-item>
                <el-form-item prop="sex" label="" :label-width="formLabelWidth">
                    <el-radio class="radio" v-model="fetchParam.sex" :label="1" :value="1"></el-radio>
                    <el-radio class="radio" v-model="fetchParam.sex" :label="2" :value="2"></el-radio>
                </el-form-item>
                <el-form-item label="" prop="professor">
                    <el-input v-model.professor="fetchParam.professor"></el-input>
                </el-form-item>

                <el-form-item  label="" :label-width="formLabelWidth" :fetch-suggestions="querySearch">
                    <el-select clearable v-model="fetchParam.hospital_id" placeholder="">
                        <el-option v-for="(item, index) in hospital_list" :label="item.name" :value="item.id" :key="item.id">
                        </el-option>
                    </el-select>
                    <!--<i></i>
                    <Hospitals v-model="fetchParam.hospital_id" :placeholder="fetchParam.hospital_id"
                                           v-on:change="val=>fetchParam.hospital_id=val" :change="getHospitalList">
                    </Hospitals>-->
                </el-form-item>
                <el-form-item label="" prop="department">
                    <el-input v-model.department="fetchParam.department"></el-input>
                </el-form-item>
                <el-form-item label="" prop="introduce">
                    <el-input v-model.introduce="fetchParam.introduce" type="textarea" :autosize="{ minRows: 3, maxRows: 7}" placeholder="">
                    </el-input>
                </el-form-item>
                <el-form-item label="" prop="goodat">
                    <el-input v-model.goodat="fetchParam.goodat" type="textarea" :autosize="{ minRows: 3, maxRows: 7}" placeholder="">
                    </el-input>
                </el-form-item>
                <el-form-item label="" prop="honor">
                    <el-input v-model.honor="fetchParam.honor" type="textarea" :autosize="{ minRows: 3, maxRows: 7}" placeholder="">
                    </el-input>
                </el-form-item>
                <el-form-item label="" >
                    <!--<el-button @click="$router.push({ name:'medical-index'})"></el-button>-->
                    <div>
                    <el-button type="primary" @click="btnNextClick"></el-button>
                    </div>
                </el-form-item>
            </el-form>
        </section>
    </main>
</template>

<script>
    import expertsService from '../../../services/course/expertsService.js'
    import commonService from '../../../services/commonService.js'
    import mineService from '../../../services/base/mineService'
    import govService from '../../../services/gov/govService.js'
    import ImagEcropperInput from '../../component/upload/ImagEcropperInputExperts.vue'
    import Hospitals from '../../component/select/Hospitals.vue'
    // import {fillImgPath} from '../../../utils/filterUtils.js'

    function clearFn() {
        return {
            typeSelect: '',
            provinceSelect: '',
            citySelect: '',
            areaSelect: '',
            townSelect: '',
            villageSelect: '',
            name: ''
        }
    }
    export default {
        name: 'course-manage-experts-edit',
        components: {
            ImagEcropperInput,Hospitals
        },
        data() {
            return {
                fillImgPath:'',
                loadingData: false,
                currentData: {
                    data: {},
                    pindex: -1,
                    index: -1
                },
                fetchParam: getOriginData(),
                // passValue: true,
                role_list:[
                    {
                        name: '',
                        id: 1
                    },
                    {
                        name: '',
                        id: 0
                    },
                ],
                rules: {
                    gov_id:{ required: true },
                    name: { required: true, message: ''},
            },
                multi: {
                    data: [{
                        id: -1
                    }],
                },
                resultData: [],
                hospital_list:[],
                formLabelWidth: '120px', // label
            }
        },
        created() {
            xmview.setContentLoading(false);
                if (this.$route.params.id != undefined) {    //id
                    // this.passValue = false
                    expertsService.getExpertsInfo(this.$route.params.id).then((ret) => {
                        this.fetchParam = ret
                        // this.fillImgPath = ret.image
                        console.log(ret.image)
                        // this.fetchParam.role_id = ret.course.role_id
                    })
                } 
            //       
             this.getHospitalList()
            this.loadingData=false
        },
        activated () {
            this.getHospitalList()
        },
        methods: {
            // passFn(){
            //     return this.$route.params.id != undefined?false:true
            // },
            // 
            cropperFn(data, ext) {
                commonService.uploadExpertsAvatar({
                    image: data,
                    alias: `${Date.now()}${ext}`,
                }).then((ret) => {
                    xmview.showTip('success', '')
                    this.fetchParam.image = data
                    this.fetchParam.image= ret.url
                }).catch((ret) => {
                    xmview.showTip('error', ret.message)
                })
            },
            
            //
            getHospitalList(val){
                expertsService.fetchHospitalData({pagesize:-1}).then((ret)=>{
                 this.hospital_list=ret.data;
                })
            },
            //
            querySearch(queryString, cb) {
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                //  callback 
                cb(results);
            },

            btnNextClick() {
                this.$refs['form'].validate((valid) => {
                    if (!valid) return
                    console.log(this.fetchParam)
                    let req = expertsService.createExperts
                    if (this.fetchParam.id) req = expertsService.updateExperts
                    req(this.fetchParam).then((ret) => {
                        // 
                        //this.$refs[fetchParam].resetFields();//
                        xmview.showTip('success', '')
                        // this.fetchParam=getOriginData(),
                        this.$refs['form'].resetFields();
                        this.currentData = {
                            data: [],
                            pindex: -1,
                            index: -1
                        }
                        if (!this.fetchParam.id) this.fetchParam.id = ret.id;
                        this.$router.go(-1)
                    })
                })
            },
            
      
        }
    }

    function getOriginData() {
        return {
            name: '',
            image:'',
            professor:'',
            hospital_id: void 0,
            department: '',
            introduce: '',
            sex: void 0,
            goodat:'',
            honor:'',

        }
    }

</script>


